.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 142px;
    height: 42px;
    padding-left: 28px;
    padding-right: 28px;

    border-radius: 20px 0px;
    border: none;
    

    font-family: inherit;
    font-size: 18px;
    line-height: 4.33;
    text-align: center;
    
    color: var(--third-text-color);
        
    
    background-color: var(--accent-color);

    cursor: pointer;

}